<template>
  <background>
    <h1>Active Women in the World</h1>
    <div>
      <Button1 @click="showAddForm = true">Add</Button1>
      <Button2 @click="showDeleteForm = true">Delete</Button2>
      <Button3 @click="showUpdateForm = true">Update</Button3>
    </div>

    <div v-if="showAddForm">
      <h2>Add Indicator</h2>
      <form @submit.prevent="addIndicator">
        <label for="country">Country:</label>
        <input type="text" id="country" v-model="newIndicator.country" />
        <label for="date">Date:</label>
        <input type="date" id="date" v-model="newIndicator.date" />
        <label for="value">Value:</label>
        <input type="number" id="value" v-model="newIndicator.value" />
        <button type="submit">Submit</button>
        <button @click="showAddForm = false">Cancel</button>
      </form>
    </div>
    <div v-if="showDeleteForm">
      <h2>Delete Indicator</h2>
      <form @submit.prevent="deleteIndicator(deleteIndicatorId)">
        <label for="indicator-id">Indicator ID:</label>
        <input type="text" id="indicator-id" v-model="deleteIndicatorId" />
        <button type="submit">Submit</button>
        <button @click="showDeleteForm = false">Cancel</button>
      </form>
    </div>
    <div v-if="showUpdateForm">
      <h2>Update Indicator</h2>
      <form @submit.prevent="updateIndicator">
        <label for="indicator-id">Indicator ID:</label>
        <input type="text" id="indicator-id" v-model="updateIndicatorId" />
        <label for="country">Country:</label>
        <input type="text" id="country" v-model="updatedIndicator.country" />
        <label for="date">Date:</label>
        <input type="date" id="date" v-model="updatedIndicator.date" />
        <label for="value">Value:</label>
        <input type="number" id="value" v-model="updatedIndicator.value" />
        <button type="submit">Submit</button>
        <button @click="showUpdateForm = false">Cancel</button>
      </form>
    </div>
    <div>
      <table>
        <thead>
          <tr>
            <th>Country</th>
            <th>Date</th>
            <th>Value</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="indicator in indicators" :key="indicator._id">
            <td>{{ indicator.country }}</td>
            <td>{{ indicator.date }}</td>
            <td>{{ indicator.value }}</td>
          </tr>
        </tbody>
      </table>
    </div>

</background>
</template>

<script>
import axios from 'axios';

export default {
  name: 'IndicatorsList',
  data() {
    return {
      indicators: [],
      newIndicator: {
        country: '',
        date: '',
        value: ''
      },
      deleteIndicatorId: '',
      updateIndicatorId: '',
      updatedIndicator: { 
        country: '',
        date: '',
        value: ''
      },
      showAddForm: false,
      showDeleteForm: false,
      showUpdateForm: false,
    };
  },
  created() {
    axios.get('http://localhost:3000/indicators')
      .then((response) => {
        this.indicators = response.data;
        console.log("indicateurs = ", this.indicators);
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    addIndicator() {
      axios.post('http://localhost:3000/indicators', this.newIndicator)
        .then((response) => {
          this.indicators.push(response.data);          
          this.showAddForm = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    deleteIndicator(id) {
      axios.delete(`http://localhost:3000/indicators/${id}`)
        .then(() => {
          this.indicators = this.indicators.filter(indicator => indicator._id !== id);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    updateIndicator() {
    const updatedIndicator = {
      country: this.updatedIndicator.country,
      date: this.updatedIndicator.date,
      value: this.updatedIndicator.value,
    };

    axios.put(`http://localhost:3000/indicators/${this.updateIndicatorId}`, updatedIndicator)
      .then(() => {
      const index = this.indicators.findIndex(indicator => indicator._id === this.updateIndicatorId);
      if (index !== -1) {
        this.indicators.splice(index, 1, updatedIndicator);
      }
      this.showUpdateForm = false;
    })
    .catch((error) => {
      console.log(error);
    });
}

  }
};
</script>

